import { useState, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";

const messages = [
    {
        id: 1,
        text: "🎥 VISITA NUESTRO CANAL DE YOUTUBE PARA DESCUBRIR MÁS SOBRE NUESTRA PROPUESTA 🎥",
        link: "https://m.youtube.com/channel/UC0SB6sPYD6cEQ1j9_8Godtg",
    },
    {
        id: 2,
        text: "💌 COMPARTE TUS IDEAS Y PROPUESTAS EN NUESTRA PÁGINA DE INSTAGRAM 💌",
        link: "",
    },
    {
        id: 3,
        text: "📜 YA DISPONIBLE NUESTRO PROGRAMA COMPLETO - DESCÁRGALO AQUÍ 📜",
        link: "/download/ProgramaGobiernoAngelesGallego.pdf",
    },
];

export default function Banner() {
    const [index, setIndex] = useState(0);

    useEffect(() => {
        const timer = setInterval(() => {
            setIndex((prev) => (prev + 1) % messages.length);
        }, 5000); // Cambia cada 5 segundos
        return () => clearInterval(timer);
    }, []);

    const current = messages[index];

    return (
        <div className="w-full bg-cyan-600 text-yellow-400 text-center py-3 overflow-hidden relative shadow-md">
            <AnimatePresence mode="wait">
                <motion.a
                    key={current.id}
                    href={current.link}
                    target={current.link.startsWith("http") ? "_blank" : "_self"}
                    rel="noopener noreferrer"
                    className="block font-semibold  md:text-xl tracking-wide px-4"
                    initial={{ opacity: 0, y: 10 }}
                    animate={{ opacity: 1, y: 0 }}
                    exit={{ opacity: 0, y: -10 }}
                    transition={{ duration: 0.6 }}
                >
                    {current.text}
                </motion.a>
            </AnimatePresence>
        </div>
    );
}
